.h_p_container{
    background: #F8F8FB;
    padding-top: 60px;
  }
  
  #h_p_create_job_container{
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 1px solid #E8E8E8;
    border-radius: 10px;
    padding: 17px 25px 21px 25px;
    margin-bottom: 42px;
  } 
  
  .h_p_create_job_title{
    font-family: muli-bold;
    font-size: 18px;
    line-height: 26px;
    margin-bottom: 6px;
  }
  
  .h_p_create_job_desc{
    font-family: muli-light;
    font-size: 13px;
    line-height: 19px;
    margin-bottom: 11px;
  }
  
  .h_p_create_job_btn{
    padding: 5px 16px;
    font-family: muli-light;
    font-size: 13px;
    line-height: 19px;
    background: transparent linear-gradient(290deg, #4FFEA3 0%, #00E7FC 100%) 0% 0% no-repeat padding-box;
    border-radius: 23px;
    border: 1px solid #00E7FC;
    cursor: pointer;
  }

  .h_p_create_job_btn:hover{
    background: white;
  }
  
