  .h_p_filter2_container{
    margin-bottom: 20px;
    margin-top: 20px;
  }
  
  .h_p_filter2_container, .h_p_filter2_dropdown_container{
    display: inline-block;
  }
  
  .h_p_filter2_input_container{
    margin-right: 20px;
    display: inline-block ;
  }
  

  .h_p_filter2_dropdown_title{
    font-family: muli-light;
    font-size: 15px;
    line-height: 22px;
    margin-top: 10px;
    margin-right: 15px;
    display: inline-block;
  }
  

  .h_p_filter1_dropdown{
    width: 11px;
    float: right;
  }
  
  .h_p_dropdown_selected{
    -webkit-transform:rotate(180deg);
    -moz-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    -o-transform: rotate(180deg);
    transform: rotate(180deg);
  }
  
  .h_p_hide_filter{
    display: none;
  }
  
  .h_p_search_suggestions{
    display: none;
    position: absolute;
    background-color: #ffffff;
    max-width: 341px;
    z-index: 1;
    max-height: 500px;
    overflow: auto;
    box-shadow: 0px 3px 6px #00000029;
    border: 1px solid #E8E8E8;
    border-radius: 20px;
    padding: 20px;
    margin-top: 5px;
  }
  
  .h_p_search_suggestions_show{
    display: block;
  }
  
  .h_p_suggestions_title{
    font-family: muli-bold;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 10px;
  }
  
  .h_p_suggestion{
    font-family: muli-light;
    font-size: 14px;
    line-height: 20px;
    padding: 5px 15px;
    margin-right: 5px;
    display: inline-block;
    border-radius: 15px;
    background: #F1F1F1;
    margin-bottom: 10px;
    cursor: pointer;
  }
  
  .h_p_start_process_form{
    padding: 0px 63px 35px 63px;
    width: 100%;
  }
  
  .h_p_start_process_form_title{
    font-family: muli-bold;
    font-size: 30px;
    text-align: center;
    width: 100%;
    margin-bottom: 20px;
    line-height: 44px;
  }
  
  .h_p_start_process_form_label{
    font-size: 18px;
    font-family: muli-bold;
    line-height: 26px;
    margin-bottom: 10px;
    cursor: pointer;
  }
  
  .h_p_start_process_form_description{
    width: calc(100% - 40px);
    height: 105px;
    margin-bottom: 30px;
    background: #F5F5F7;
    border-radius: 10px;
    border: 1px solid white;
    padding: 10px 20px;
    font-size: 14px;
    font-family: muli-light;
  }
  
  .h_p_start_process_form_file{
    display: none;
  }
  
  .h_p_start_process_form_file_label{
    display: inline-block;
    background: #DBDBDB;
    font-size: muli-light;
    padding: 10px 44px;
    border-radius: 10px;
    margin-right: 20px;
    cursor: pointer;
  }
  
  .h_p_start_process_form_file_label_text{
    display: inline-block;
    font-family: muli-light;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
  }
  
  .h_p_start_process_form_btn_container{
    text-align: center;
  }
  
  .h_p_start_process_form_btn{
    background: #4FFEA3;
    border-radius: 23px;
    font-family: muli-regular;
    font-size: 16px;
    border: 0;
    padding: 10px 73px;
    cursor: pointer;
  }

